'use strict';

import React, {PropTypes} from 'react';
import ResourceText from './ResourceText';
import url from 'url';

const BotifyResourceTextFormatter = React.createClass({

  displayName: 'BotifyResourceTextFormatter',

  propTypes: {
    id: PropTypes.string.isRequired, //used to create helper if doesn't exist
    component: PropTypes.func.isRequired,
    resourceText: PropTypes.shape({
      defaultText: PropTypes.string,
      text: PropTypes.string,
      defaultDescription: PropTypes.string,
      description: PropTypes.string,
      addUrl: PropTypes.string,
      editUrl: PropTypes.string,
    }).isRequired,
  },

  render() {
    let {
      id: id,
      component: Component,
      resourceText: {defaultText, text, defaultDescription, description, addUrl, editUrl},
      ...otherProps
    } = this.props;

    let resourceText = {
      text: text || defaultText,
      description: description || defaultDescription,
      editUrl: editUrl || addUrl ? url.resolve(addUrl, id) : undefined
    };

    return (
      <Component
        {...otherProps}
        resourceText={resourceText}
      />
    );
  }

});


const BotifyResourceText = React.createClass({

  displayName: 'BotifyResourceText',

  propTypes: {
    id: PropTypes.string,
    resourceText: PropTypes.shape({
      defaultText: PropTypes.string,
      text: PropTypes.string,
      defaultDescription: PropTypes.string,
      description: PropTypes.string,
      addUrl: PropTypes.string,
      editUrl: PropTypes.string,
    }).isRequired,
    isAdmin: PropTypes.bool,
  },

  render() {
    return (
      <BotifyResourceTextFormatter
        {...this.props}
        component={ResourceText}
      />
    );
  }

});

export default BotifyResourceText;
export {BotifyResourceTextFormatter};
